body {
    background-color: #9572FF;
}

.App {
    background-color: #3F3360;
    min-height: 500px;
    width: 400px;
    margin: 40px auto;
}

#to-do-form input {
    background-color: rgb(27, 112, 137);
    padding: 0 25px;
    margin: 20px;
    width: 200px;
    height: 50px;
    font-size: 18px;
    border-radius: 10px;
    color: #fff;
    border: 0;
}

#to-do-form input::placeholder {
    color: rgba(255, 255, 255, 0.5)
}

#to-do-form input:focus,
button:focus {
    outline: none;
}

#to-do-form button {
    height: 50px;
    width: 80px;
    border-radius: 5px;
    border: 0;
    background-color: rgb(202, 156, 24);
    font-weight: bolder;
}